.psv-settings-title {
  display: flex;
  align-items: center;
  font: 24px sans-serif;
  margin: 24px 0; }
  .psv-settings-title svg {
    width: 24px;
    height: 24px;
    margin: 0 12px; }

.psv-settings-list {
  list-style: none;
  margin: 0;
  padding: 0;
  overflow: hidden; }

.psv-settings-item {
  clear: both;
  min-height: 20px;
  padding: .5em 1em;
  cursor: pointer;
  display: flex;
  align-items: center; }
  .psv-settings-item:hover {
    background: rgba(255, 255, 255, 0.1); }
  .psv-settings-item-label {
    flex: 1;
    font-weight: bold; }
  .psv-settings-item-value {
    flex: none; }
  .psv-settings-item-icon {
    flex: none;
    height: 1em;
    width: 1em; }
    .psv-settings-item-icon svg {
      width: 1em;
      height: 1em; }
    .psv-settings-item-icon:first-child {
      margin-right: 1em; }
    .psv-settings-item-icon:last-child {
      margin-left: 1em; }
  .psv-settings-item--header {
    border-bottom: 1px solid currentColor;
    margin-bottom: 1em; }
    .psv-settings-item--header svg {
      transform: scaleX(-1); }
